.App-dark {
  transition: all 0.5s ease;
  height: 100vh;
  background: black;
  color: rgb(255, 255, 255);
  nav {
    button,
    h1 {
      color: rgb(241, 241, 241);
    }
  }
  .song-container {
    h2 {
      color: rgb(241, 241, 241);
    }
    h3 {
      color: rgb(201, 201, 201);
    }
  }
  .library {
    background: black;
    box-shadow: 2px 2px 50px rgb(41, 41, 41);
    h2 {
      color: rgb(255, 255, 255);
    }
  }
  .volume-container {
    background: rgba(150, 150, 150, 0.5);
  }
  .library-song {
    &:hover {
      background: rgb(37, 37, 37);
    }
  }
  .song-description {
    h3 {
      color: rgb(241, 241, 241);
    }
    h4 {
      color: rgb(201, 201, 201);
    }
  }
  .selected {
    background: hsl(0, 0%, 20%);
  }
}
