.song-container {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  img {
    width: 20%;
    border-radius: 50%;
  }
  h2 {
    padding: 3rem 1rem 1rem 1rem;
  }
  h3 {
    font-size: 1rem;
  }
}

//VOLUME
.volume-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  justify-content: center;
  align-items: center;
  column-gap: 1rem;
  background: rgba(0, 0, 0, 0.5);
  width: 50vw;
  height: 10vh;
  border-radius: 0.25rem;
  color: white;

  .volume-range-container {
    width: 75%;
    position: relative;
    height: 1rem;
    border-radius: 1rem;
    overflow: hidden;
    .volume-range {
      -webkit-appearance: none;
      background-color: white;
      width: 100%;
      border-radius: 1rem;
    }
    .volume-range::-webkit-slider-thumb {
      -webkit-appearance: none;
      height: 16px;
      width: 16px;
      border: none;
    }
    .volume-range::-moz-range-thumb {
      -webkit-appearance: none;
      height: 16px;
      width: 16px;
      border: none;
    }
    .volume-range-animate {
      width: 100%;
      height: 100%;
      border-radius: 1rem;
      position: absolute;
      top: 0;
      right: 100%;
      pointer-events: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .song-container {
    img {
      width: 60%;
    }
  }
  .volume-container {
    width: 90%;
  }
}
